.app {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  align-items: center;
  justify-content: center;

  width: 100%;
  max-width: 1050px;
  margin: 0 auto;
  padding: 0 15px;
}

.appContent {
  overflow: hidden;
  display: flex;
  flex-grow: 1;

  width: 100%;
  height: 100vh;

  border-radius: 20px;
}
